import popModal from '../../../../components/popmodal.vue'
export default {
	data() {
		return {
			telephone:'',
			checked:true,
			isShow:false
		}
	},
	components:{
		popModal
	},
	methods: {
		refuse(){
			this.checked=false;
		},
		changeTelephone(){
			if(!this.telephone){
				uni.showToast({
					title:'请输入电话号码',
					icon:'none'
				})
			}else{
				this.isShow=true;
			}
			
		},
		hideModal(){
			this.isShow=false;
		},
		checkTelEvent(event){
			if(!(/^1(3|4|5|6|7|8)\d{9}$/.test(event))){ 
				uni.showToast({
					title:'输入电话号码有误',
					icon:'none'
				})
				return;
			}
		},
		inputCheckCard(){
			//输入验证码
			uni.navigateTo({
				url:'../input-code/input-code'
			})
		}
	}
}
